.sidebarOption {
    display: flex;
    align-items: center;
    color: grey;
    height: 40px;
    cursor: pointer;
    transition: 200ms color ease-in;

    &:hover {
        color: white;
    }

    .sidebarOption__icon {
        padding-right: 10px;
        padding-left: 10px;
    }

    p {
        margin-top: 10px;
        margin-left: 20px;
        font-size: 14px;
    }
}